<template>
  <div id="basketcom">
    <div class="wrpBasket" v-show="clothes">
        <div class="container">
            <div class="offersMain wrpClotheBar">
              <div class="product_bar_item"
              >
                <div>
                    <img class="clotheBarImg" :src="require('@/assets/images/' + clothes.img)" alt="">
                    <h5 class="textBar">{{ clothes.title }}</h5>
                    <h5 class="priceItem">{{clothes.price}}₽</h5>
                    <img src="@/assets/images/sale.png" v-show="clothes.oferta" class="salePic">
                </div>
              </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import { bus } from '../main'

export default {
  name: 'BasketCom',
  components: {
  },
  data () {
    return {
      clothes: '',
      dataItem: '',
      clotheBasket: ''
    }
  },
  methods: {
  },
  created() {
    bus.$on('changeIt', result => {
      for (let i = 0; i < result.length; i += 1) {
        if (typeof (result[i]) === 'object') {
          try {
            result[i] = JSON.parse(JSON.stringify(result[i]));
            this.clothes = result[i]
            console.log(this.clothes)
            this.clotheBasket.push(this.clothes)
            console.log(this.clotheBasket)
          } catch (e) {
            console.error(e);
          }
        }
      }
    })
  }
}
</script>

<style scoped lang="scss">
.wrpBasket {
    min-height: 400px;
}
</style>
