<template>
  <div id="openCard">
    <Menu />
    <div class="wrpClotheItem" v-for="item in newClothes" :key="item.index" v-show="item.id == currentID">
      <h4 class="titleItem" v-if="item.id == currentID">{{item.title}}</h4>
      <div v-if="item.id == currentID" class="wrpDescription">
        <img class="imgItem" v-if="item.id == currentID" :src="require('@/assets/images/' + item.img)" alt="">
        <div class="description">
          <div class="wrpDesc">
            <h4 class="titleOfTypeInfo">Description:</h4>
            <p v-if="item.id == currentID">{{item.description}}</p>
          </div>
          <div class="wrpInWhat">
            <h4 class="titleOfTypeInfo">Composition:</h4>
            <ul>
              <li v-if="item.id == currentID">&#9675; {{item.inWhat.item1}}</li>
              <li v-if="item.id == currentID">&#9675; {{item.inWhat.item2}}</li>
              <li v-if="item.id == currentID">&#9675; {{item.inWhat.item3}}</li>
            </ul>
          </div>
          <div class="wrpDesc">
            <h4 class="titleOfTypeInfo">Price:</h4>
            <p class="saleItem" v-if="item.id == currentID && item.price">Sale -{{item.oferta}}</p>
            <p class="priceItem" v-if="item.id == currentID">{{item.price}}$</p>
          </div>
          <div class="wrpAddBusket">
            <button 
            @click="addToBasket"
             class="red accent-1 btn">Add to busket</button>
          </div>
        </div>
      </div>
    </div>
    <BasketCom/>
    <Footer />
  </div>
</template>

<script>
import Menu from '@/components/Menu.vue'
import Footer from '@/components/Footer.vue'
import axios from 'axios'
import BasketCom from '@/components/BasketCom.vue'
import { bus } from '../main'
const baseUrl = "http://localhost:3000/newClothes"

export default {
  name: 'OpenCard',
  components: {
    Menu,
    Footer,
    BasketCom
  },
  data () {
    return {
         newClothes: [],
         title: '',
         currentID: this.$route.params.id,
        result:{
          type: String
        } 
    }
  },
  async created() {
    try{
      const res = await axios.get(baseUrl);

      this.newClothes = res.data;
    } catch (e) {
      console.error(e);
    }
  },
  computed: {
    clothe() {
      return this.$store.getters.clotheById(+this.$route.params.id)
    }
  },
  methods: {
    addToBasket() {
      let id = this.currentID
      var resultone = this.newClothes.filter(clothe => clothe.id == id)
      this.result = resultone
      bus.$emit('changeIt', this.result);
    }
  }
}
</script>

<style scoped lang="scss">
@import './../assets/scss/styles.scss';

.wrpClotheItem {
  max-width: 80%;
  margin: 0 auto;
  padding-bottom: 1rem;
}

.imgItem {
  max-width: 300px;
  border: 1px solid grey;
}

.wrpDescription {
  margin: 3rem 0;
  @include for-medium-up {
    display: flex;
  }
}

.description {
  @include for-medium-up {
    margin-left: 3rem;
  }

}

.titleItem {
  color: black;
  font-style: italic;
}

.titleOfTypeInfo {
  margin: 0;
  text-align: left;
  font-weight: 600;
  font-size: 18px;
}

ul {
  list-style-type: none;
  text-align: left;
}

.wrpDesc {
  align-items: left;
  p {
    text-align: left;
  }
}

.wrpInWhat {
  margin-top: 2rem;
}

.priceItem {
  color: darkred;
  font-size: 25px;
  font-weight: 600;
  margin: 0;
}

.saleItem {
  margin-top: 0.8rem;
  margin-bottom: 0;
  font-weight: 600;
  color: green;
}

.wrpAddBusket {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-start;
}
</style>
